header {
	background-color: rgb(224,238,250);

	.headerinner {
		@extend %wrapping;

		nav {
			a {
				@extend %navig;
				transform: translate(-130px, 0px); } }

		.highlightorange {
			@extend %highlightorange; }

		.header {
			@extend %container;
			flex-direction: row;
			justify-content: space-between;
			align-items: center;

			.logo {
				img {
					max-height: 260px;
					width: auto; } }

			.personalinfo {
				padding: 0px;
				margin: 0px;

				h1 {
					@extend %titleh1; }

				h2 {
					font: {
						family: 'Quicksand', sans-serif;
						size: 18px;
						weight: 500; }
					color: rgba(0,0,0,0.7);
					letter-spacing: 0.02em;
					padding: 0.2px;
					margin: 0px; }

				a {
					font: {
						family: 'Quicksand', sans-serif;
						size: 18px;
						weight: 100; }
					margin: 10px 10px 0px 0px;
					color: rgba(0,0,0,0.7);
					background-color: rgba(0,238,0,0.1);

					&:hover {
						transition: 0.5s all;
						color: rgba(0,0,0,0.9);
						background-color: rgba(0,238,0,0.2); } }

				p {
					@extend %titlep; } } } } }
