%wrapping {
	display: block;
	margin: 0 auto;
	max-width: 900px; }

%container {
	display: flex;
	padding: 15px 50px 40px 50px;
	box-sizing: border-box;
	width: 100%; }

%navig {
	float: right;
	display: block;
	margin: 17px 22px 0px 22px;
	transform: translate(-90px, 0px);
	font: {
		family: 'Quicksand', sans-serif;
		size: 18px;
		weight: 500; }
	color: rgba(0,0,0,0.7);
	text-transform: lowercase;

	&:hover {
		transition: 0.6s all;
		color: rgba(0,0,0,1.0);
		background-color: rgba(246,100,0,0.1); } }



%highlightorange {
	background-color: rgba(246,100,0,0.1);
	background-image: linear-gradient(to right, rgba(246,100,0,0.1), rgba(246,100,0,0.0)); }

%highlightblue {
	background-color: rgba(224,238,250,0.4);
	background-image: linear-gradient(to right, rgba(224,238,250,0.4), rgba(224,238,250,0.1)); }

%highlightgreen {
	background-color: rgba(0,238,0,0.1); }

%highlightyellow {
	background-color: rgba(255, 252, 224, 0.7); }



