.postouter {
	background-color: rgb(224,238,250);

	.postinner {
		@extend %wrapping;

		nav {
			a {
				@extend %navig; } }

		.highlightorange {
			@extend %highlightorange; }

		.highlightgreen {
			@extend %highlightgreen; }

		.postheader {
			@extend %container;
			flex-direction: column;
			justify-content: space-between;


			h1 {
				@extend %titleh1; }

			p {
				@extend %titlep;
				color: rgba(0,0,0,0.6); } } } }
