.booklistouter {
	background-color: rgb(255, 252, 224);

	.booklistinner {
		@extend %wrapping;

		.booklist {
			@extend %container;
			flex-direction: row;
			align-items: flow-start;
			justify-content: space-around;

			.projects {
				h2 {
					text-align: center;
					text-decoration: underline;
					text-decoration-color: rgba(246,100,0,0.3);
					@extend %sectiontitle; }

				p {
					margin: 20px 0px;
					font: {
						family: "Roboto Slab", serif;
						size: 16px;
						weight: 400; }
					color: rgba(0,0,0,0.7); }

				.date {
					font: {
						family: "Quicksand", sans-serif;
						size: 14px;
						weight: 300; }
					background-color: rgba(0,238,0,0.1); }

				table {
					table-layout: fixed;
					border-collapse: collapse;
					margin: 30px 0px;

					table {
						table-layout: fixed;
						margin: 0px 0px 20px 0px; } }

				tbody>tr>:nth-child(1) {
					vertical-align: top;
					font: {
						family: "Roboto Slab", serif;
						size: 18px;
						weight: 600; } }

				tbody>tr>:nth-child(2) {
					vertical-align: top;
					font: {
						family: "Roboto Slab", serif;
						size: 16px;
						weight: 300; } } } } } }





