.postmainouter {
	background-color: white;

	.postmaininner {
		@extend %wrapping;

		.postmain {
			@extend %container;
			padding: 15px 50px 0px 50px;
			flex-direction: column;
			justify-content: space-between;

			.highlightyellow {
				@extend %highlightyellow; }

			ul {
				list-style-type: none;

				li {
					margin: 23.25px 0px;
					a {
						background-color: rgba(246,100,0,0.0);
						margin: 23.24px 0px;
						line-height: 100%;
						@extend %sectiontitle;

						&:hover {
							transition: 0.4s all;
							color: rgba(0,0,0,0.4); } }
					p {
						@extend %sectiontext;
						color: rgba(0,0,0,0.8);
						padding: 0px 20px;
						margin: 10px 0px 20px 0px;
						border-left: 0.6px rgba(0,0,0,0.3) solid; }

					.taglink {
						a {
							font: {
								family: "Courier New";
								size: 12px; }
							color: white;
							background-color: rgb(234, 0, 0);
							padding: 2px;
							margin: 10px 4px 0px 0px;
							border-radius: 3px;

							&:hover {
								trasition: 0.2s all;
								background-color: white;
								color: rgb(234,0,0); } } } } } } } }


