.posthead {
	.postheadouter {
		background-color: rgb(255, 252, 224);

		.postheadinner {
			@extend %wrapping;

			nav {
				a {
					@extend %navig;
					&:hover {
						background-color: rgba(224,238,250,0.6); } } }

			.posthead {
				@extend %container;
				flex-direction: column;
				justify-content: space-between;

				h1 {
					@extend %titleh1;
					text-decoration: underline;
					text-decoration-color: rgb(63, 101, 145);
					//rgba(224,238,250,0.6)
					font-size: 42px; }

				p {
					@extend %titlep;
					font-size: 16px;
					color: rgba(0,0,0,0.6); } } } } }

.posttextouter {
	background-color: white;

	.posttextinner {
		@extend %wrapping;

		.posttext {
			@extend %container;
			flex-direction: column;
			justify-content: space-between;

			h2 {
				@extend %sectiontitle; }

			h3 {
				@extend %sectionsubtitle1; }

			h4 {
				@extend %sectionsubtitle2; }

			p {
				@extend %sectiontext;
				margin: 11px 0px; }

			img {
				display: flex;
				margin: 0 auto;
				padding: 0px 0px 20px 0px;
				max-width: 70%;
				height: auto; }

			a {
				@extend %highlightgreen;
				color: rgba(0,0,0,1.0);

				&:hover {
					transition: 0.4s all;
					color: rgba(0,0,0,0.7);
					background-color: rgba(0,238,0,0.2); } }

			.taglink {
				a {
					font: {
						family: "Courier New";
						size: 12px;
						weight: 600; }
					color: white;
					background-color: rgb(234, 0, 0);
					padding: 2px;
					margin: 10px 4px 0px 0px;
					border-radius: 3px;

					&:hover {
						trasition: 0.2s all;
						background-color: white;
						color: rgb(234,0,0); } } } } } }



