.aboutouter {
	background-color: white;

	.aboutinner {
		@extend %wrapping;

		.about {
			@extend %container;
			display: flex;
			flex-direction: column;
			align-items: flow-start;

			h2 {
				text-align: center;
				text-decoration: underline;
				text-decoration-color: rgba(246,100,0,0.3);
				@extend %sectiontitle; }

			p {
				text-align: left;
				@extend %sectiontext; } } } }


