.taghead {
	.tagheadouter {
		background-color: rgb(255, 252, 224);

		.tagheadinner {
			@extend %wrapping;

			nav {
				a {
					@extend %navig;
					&:hover {
						background-color: rgba(224,238,250,0.6); } } }

			.taghead {
				@extend %container;
				flex-direction: column;
				justify-content: space-between;

				h1 {
					@extend %titleh1;
					text-decoration: underline;
					text-decoration-color: rgb(63, 101, 145);
					//rgba(224,238,250,0.6)
					font-size: 42px; }

				p {
					@extend %titlep;
					font-size: 16px;
					color: rgba(0,0,0,0.6); } } } } }

.tagtextouter {
	background-color: white;

	.tagtextinner {
		@extend %wrapping;

		.tagtext {
			@extend %container;
			padding: 15px 50px 0px 50px;
			flex-direction: column;
			justify-content: space-between;

			.highlightyellow {
				@extend %highlightyellow; }

			ul {
				list-style-type: none;

				li {
					margin: 23.25px 0px;
					a {
						background-color: rgba(246,100,0,0.0);
						margin: 23.24px 0px;
						line-height: 100%;
						@extend %sectiontitle;

						&:hover {
							transition: 0.4s all;
							color: rgba(0,0,0,0.4); } }
					p {
						@extend %sectiontext;
						color: rgba(0,0,0,0.8);
						padding: 0px 20px;
						margin: 10px 0px 50px 0px;
						border-left: 0.9px rgb(112, 154, 69) solid; } } } } } }


